import * as React from 'react';
import Box from '@mui/material/Box';
import Tabs, { tabsClasses } from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';

import FireTruckIcon from '@mui/icons-material/FireTruck';
import LocalShippingIcon from '@mui/icons-material/LocalShipping';
import PrecisionManufacturingIcon from '@mui/icons-material/PrecisionManufacturing';
import MoreIcon from '@mui/icons-material/More';

export default function IconPositionTabs() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 1 }}>
            <Tabs
                value={value}
                onChange={handleChange}
                variant="scrollable"
                scrollButtons
                allowScrollButtonsMobile
                aria-label="category choose"
                sx={{
                    [`& .${tabsClasses.scrollButtons}`]: {
                        '&.Mui-disabled': { opacity: 0.3 },
                    },
                }}
            >
                <Tab icon={<FireTruckIcon />} iconPosition="start" label="" />
                <Tab icon={<LocalShippingIcon />} iconPosition="start" label="" />
                <Tab icon={<PrecisionManufacturingIcon />} iconPosition="start" label="" />
                <Tab icon={<MoreIcon />} iconPosition="start" label="" />
            </Tabs>
        </Box>
    );
}
