import * as React from 'react';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import ListSubheader from '@mui/material/ListSubheader';
import IconButton from '@mui/material/IconButton';
import InfoIcon from '@mui/icons-material/Info';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { styled } from '@mui/material/styles';

function srcset(image, width, height, rows = 1, cols = 1) {
  return {
    src: `${image}?w=${width * cols}&h=${height * rows}&fit=crop&auto=format`,
    srcSet: `${image}?w=${width * cols}&h=${height * rows}&fit=crop&auto=format&dpr=2 2x`,
  };
}

// Custom styled component for featured items
const FeaturedImageListItem = styled(ImageListItem)(({ theme }) => ({
  border: `2px solid ${theme.palette.primary.main}`,
  borderRadius: theme.shape.borderRadius,
  boxShadow: theme.shadows[3],
  overflow: 'hidden',
  position: 'relative',
  '&:before': {
    content: '""',
    position: 'absolute',
    top: 0,
    left: 0,
    width: '100%',
    height: '100%',
    background: 'linear-gradient(145deg, rgba(0,0,0,0.3), rgba(0,0,0,0.1))',
    zIndex: 1,
  },
  '& img': {
    transition: 'transform 0.3s ease-in-out',
  },
  '&:hover img': {
    transform: 'scale(1.1)',
  },
  '& .MuiImageListItemBar-root': {
    background: 'rgba(0, 0, 0, 0.7)',
    zIndex: 2,
  },
}));

export default function TitlebarImageList() {
  const theme = useTheme();
  const isXs = useMediaQuery(theme.breakpoints.only('xs'));
  const isSm = useMediaQuery(theme.breakpoints.only('sm'));
  const isMd = useMediaQuery(theme.breakpoints.only('md'));
  const isLg = useMediaQuery(theme.breakpoints.only('lg'));
  const isXl = useMediaQuery(theme.breakpoints.only('xl'));

  let cols = 4; // default number of columns
  if (isXs) cols = 1;
  else if (isSm) cols = 2;
  else if (isMd) cols = 3;
  else if (isLg) cols = 4;
  else if (isXl) cols = 5;

  return (
    <ImageList sx={{ width: '100%', height: 'auto' }} cols={cols} gap={8}>
      {/*<ImageListItem key="Subheader" cols={cols} style={{ height: 'auto' }}>
        <ListSubheader component="div"> </ListSubheader>
      </ImageListItem>*/}
      {itemData.map((item) => {
        const { src, srcSet } = srcset(item.img, 248, 248, item.rows, item.cols);
        const ItemComponent = item.featured ? FeaturedImageListItem : ImageListItem;

        return (
          <ItemComponent key={item.img} cols={item.cols || 1} rows={item.rows || 1}>
            <img
              src={src}
              srcSet={srcSet}
              alt={item.title}
              loading="lazy"
            />
            <ImageListItemBar
              title={item.title}
              subtitle={item.author}
              actionIcon={
                <IconButton
                  sx={{ color: 'rgba(255, 255, 255, 0.54)' }}
                  aria-label={`info about ${item.title}`}
                >
                  <InfoIcon />
                </IconButton>
              }
            />
          </ItemComponent>
        );
      })}
    </ImageList>
  );
}

